<template>
<div class="content-wrapper" id="home">
    <div class="container">
		<carousel v-if="banner.length" :responsive="{0:{items:1,nav:false,loop:false, autoplay: true}}" class="owl-product" id="home-slide">
			<router-link v-for="item in banner" :key="item.id" :to="item.url" :title="item.name">
				<img class="item" :src="api+'/image/banner/'+item.image">
			</router-link>
		</carousel>
		<div class="main-content">
			<div class="row">
				<div class="col-lg-9 col-md-9">
					<h5 class="title">CÁC DANH MỤC CHÍNH</h5>
					<div class="content categorys">
						<Category :category="category" />
						<div class="row" style="display:none;">
							<div class="tizi-introduce">
								Tizi.vn là kênh mua bán bất động sản, việc làm, rao vặt. Được quản lý và vận hành bởi Công ty Cổ phần Đầu tư và Thương mại VNCT.<br>Được thành lập măm 2017 với khẩu hiệu ''Tìm gì đã có Tizi'' đến hết nay Tizi đã tối ưu tất cả các danh mục nhằm phục vụ việc tìm kiếm và đánh giá sản phẩm, giúp quý khách hàng lựa chọn được sản phẩm với giá hời tốt nhất trên thị trường.
								<div style="text-align:center"><em>Chúc quý khách mua may bán đắt cùng Tizi</em></div>
							</div>
						</div>
					</div>
					<h5 class="title">CÁC TIN MỚI ĐĂNG</h5>
					<Product :product="product" />
				</div>
				<advertisement />
			</div>
		</div>
    </div>
</div> 
</template>

<script>
import {mapGetters} from "vuex";
import advertisement from "../advertisement/Index";
import carousel from 'vue-owl-carousel';
import axios from "axios";
import moment from "moment";
import Category from "./Category";
import Product from "./Product";

export default {
	metaInfo(){
		return {
			title: "Tizi.vn - Website rao vặt đăng tin và đẩy tin miễn phí toàn quốc",
			htmlAttrs: {
				lang: 'vi',
				amp: true
			},
			meta: this.meta
		}
	},
	name: "Home",
	components: {advertisement, carousel, Category, Product},
	data() {
		return {
			api: this.$store.state.domainApi,
			meta: [],
			province: "toan-quoc",
			banner:[],
			product:[]
		}
	},
	computed:{
		...mapGetters({category:'getCategoryParent'})
	},
	methods:{
		getTime(date){
            var time = moment(date);
            return time.fromNow();
        },
		number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        }
	},
	created() {
		axios.get(this.api+"/public/banner")
		.then(response=>{
			this.banner = response.data;
		});
		this.meta = [
			{charset: "utf-8"},
			{
				"property": "og:title",
				"content": "Tizi.vn - Website rao vặt đăng tin và đẩy tin miễn phí toàn quốc",
			},
			{
				"property": "og:description",
				"content": "Tizi.vn - Website rao vặt đăng tin và đẩy tin miễn phí toàn quốc",
			},
			{
				"name": "description",
				"content": "Tizi.vn - Website rao vặt đăng tin và đẩy tin miễn phí toàn quốc",
			}
		];
	},
	mounted(){
		axios.get(this.api + "/public/product",{
			params: {
				id_province: "toan-quoc",
				id_category: "tat-ca-danh-muc",
				sortOption: "time-new",
				group: "tat-ca",
				page: 1,
				pageSize: 10
			}
		})
		.then(response=>{
			this.product = response.data.product;
		})
	}
};
</script>

