<template>
<div class="product-new content-body">
    <div v-for="(item,index) in product" :key="index+1" :class="{item: true, priority: item.product_priority}">
        <div class="row">
            <div class="col-lg-4 col-md-4 col-sm-5 col-xs-5">
                <router-link :to="{name:'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url }}" v-bind:title="item.name">
                    <img class="img" v-bind:src="api + '/image/product/thumbnail/' + item.image" v-bind:alt="item.name">
                </router-link>
            </div>
            <div class="col-lg-8 col-md-8 col-sm-7 col-xs-7">
                <router-link :to="{name:'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url }}" v-bind:title="item.name">
                    <h2 :class="{title: true, priority: item.product_priority}">{{ item.name }}</h2>
                    <span class="product-type default cat">
                        <i class="fa fa-fw fa-indent"></i> {{ item.category }}
                    </span>
                    <span v-if="item.product_priority" class="product-type">
                        <i class="fa fa-star"></i>Hot
                    </span>
                    <div class="about">
                        <span v-if="item.price!=0" class="price">{{ number_format(item.price, 0) }} đ</span>
                        <span v-if="item.price==0" class="price">Thỏa thuận</span>
                        <div>
                            <span v-if="!item.product_priority" class="product-time"><i class="fa fa-clock-o" aria-hidden="true"></i> {{ getTime(item.date) }}</span>
                            <span v-if="item.product_priority" class="product-time"><i class="fa fa-buysellads" aria-hidden="true"></i> Quảng cáo</span>
                            <span class="product-address"><i class="fa fa-map-marker" aria-hidden="true"></i> {{ item.province }}</span>
                            <span class="product-user"><i :class="{fa:true, 'fa-archive': item.is_vip, 'fa-user': !item.is_vip}"></i> {{ item.username }}</span>
                        </div>
                    </div>
                </router-link>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import moment from "moment";

export default {
    props:["product"],
    data() {
        return {
            api: this.$store.state.domainApi
        }
    },
	methods:{
		getTime(date){
            var time = moment(date);
            return time.fromNow();
        },
		number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        }
	},
}
</script>

